body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', "Arial", sans-serif;
  font-size: 15px;
  line-height: 15px;
  font-weight: 400;
  color: #2B2A2C;
}
body.no-scroll {
  overflow: hidden;
}
a {
  text-decoration: none;
}
img {
  width: 100%;
  height: auto;
}
.header {
  position: relative;
  box-sizing: border-box;
  padding: 18px 0;
}
@media (max-width: 767px) {
  .header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    background-color: #FFFFFF;
    box-shadow: 0 3px 7px rgba(0, 0, 0, 0.3);
  }
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__user {
  display: flex;
}
.header__user-avatar {
  width: 57px;
  height: 57px;
  border-radius: 50%;
}
.header__list {
  margin: 0;
  padding: 0 0 0 9px;
  list-style: none;
}
.header__item-name {
  margin-bottom: 3px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  color: #2B2A2C;
}
.header__item {
  font-size: 12px;
  color: rgba(43, 42, 44, 0.7);
}
.header__nav-list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  text-align: center;
  height: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .header__nav-list {
    position: absolute;
    top: 100%;
    right: 0;
    z-index: 1000;
    width: 100%;
    height: auto;
    display: none;
    text-align: center;
    background-color: #FFFFFF;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
  }
  .header__nav-list.show {
    display: flex;
  }
}
.header__nav-item {
  margin-right: 30px;
}
.header__nav-item:last-child {
  margin-right: 0;
}
@media (max-width: 767px) {
  .header__nav-item {
    display: block;
    width: 100%;
    padding: 7px 15px;
    margin-right: 0;
  }
}
.header__nav-link {
  position: relative;
  padding: 0 0 5px;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  color: #2B2A2C;
}
.header__nav-link::after {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 10;
  width: 0;
  height: 2px;
  content: "";
  background-color: #2B2A2C;
  transition: width 0.2s linear;
}
.header__nav-link:hover:not(.header__nav-link--btn)::after {
  width: 100%;
}
.header__nav-link--btn {
  padding: 10px 30px;
  border: 2px solid #2B2A2C;
  border-radius: 20px;
  transition: background-color 0.2s linear, color 0.2s linear;
}
.header__nav-link--btn:hover {
  background-color: #2B2A2C;
  color: #ffffff;
}
@media (max-width: 767px) {
  .header__nav-link {
    display: block;
    box-sizing: border-box;
    width: 100%;
    margin-left: 0;
    padding: 0;
  }
}
.promo__wrapper {
