    display: none;
  }
}
.promo__social-list {
  display: flex;
  align-items: center;
  padding: 0;
  margin: 0 0 42px;
  list-style: none;
}
@media (max-width: 565px) {
  .promo__social-list {
    flex-wrap: wrap;
  }
}
.promo__social-item {
  margin-right: 20px;
  width: 40px;
  height: 40px;
  transition: transform 0.2s linear;
}
.promo__social-item:hover {
  transform: translateY(-5px);
}
.promo__social-link {
  display: block;
  width: 40px;
  height: 40px;
}
.works {
  padding: 40px 0;
  margin-bottom: 20px;
}
.works__nav-list {
  display: flex;
  justify-content: center;
  padding: 0;
