}
.works__nav-link:hover {
  text-decoration: underline;
}
.portfolio {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
  padding: 0 0 42px;
  list-style: none;
}
@media (max-width: 565px) {
